      <span class="tip"
        >العناصر المحاطة باللون الأحمر تعني أن الطالب قام بأداء الأختبار ولكن انهاه بعد
        مرور الوقت المحدد</span
      >
    </div>
    <div class="_inputs1">
      <div class="row justify-content-center" dir="rtl">
        <div class="col-md-4">
          <div class="form-group">
            <label>الحالة</label>
            <select class="form-control" v-model="filter.done">
              <option :value="true">تم التصحيح</option>
              <option :value="false">بأنتظار التصحيح</option>
              <option value="all">all</option>
            </select>
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label>username</label>
            <input
              type="text"
              class="form-control"
              placeholder="username"
              v-model="filter.username"
            />
          </div>
        </div>
        <div class="col-md-4">
          <div class="form-group">
            <label>phone</label>
            <input
              type="text"
              class="form-control"
              placeholder="search"
              v-model="filter.phone"
            />
          </div>
        </div>
      </div>
      <hr />
      <div class="row justify-content-center">
        <div class="col-md-4 center">
          <button @click="filter = { ...filter }" class="btn btn-success">Find</button>
        </div>
      </div>
    </div>
    <hr />
    <div>
      <server-responsive-table
        title="Answers"
        :api="api"
        itemsName="solutions"
        :returnedItems="students"
        :colsTitles="[
          'username',
          'phone',
          'exam name',
          'unit',
          'total degree',
          'full degree',
        ]"
        :cols="['username', 'phone', 'name', 'unit', 'totalDegree', 'fullDegree']"
        :colsTypes="[
          'readonly',
          'readonly',
          'readonly',
          'readonly',
          'readonly',
          'readonly',
        ]"
        :btns="[
          { icon: 'pencil', method: getAnswers },
          { icon: 'trash', method: deleteAnswer },
        ]"
        color="#555"
        :filter="filter"
        @itemsFetched="(items) => (filteredStudents = items)"
        :setRowStyle="
          (item) => {
            return !item.inTime ? 'border: 4px solid red' : '';
          }
        "
      />
    </div>
    <!------------------------------->
    <!------------------------------->
  </div>
</template>

<script>
import Axios from "axios";
import { eventBus } from "../../../main";
import Solution from "./solution.vue";
export default {
  created() {
    const examId = this.$route.params.examId;

    this.api = `/getExamStudentsSolutions/${examId}`;

    eventBus.$on("delete-solution", (solutionId) => {
      const index = this.students.findIndex((stud) => stud.solution?._id == solutionId);
      this.students[index]["solution"] = null;
      this.students[index]["done"] = false;
      this.filter = { ...this.filter };
    });
  },
  data() {
    return {
      api: "",
      loading: false,
      students: [],
      filteredStudents: [],
      solved: true,
      filter: {
        done: true,
        username: "",
        phone: "",
      },
    };
  },
  watch: {},
  methods: {
    getAnswers(item) {
      Axios.get(`/getUnitExamSolutionById/${item._id}`).then((res) => {
        eventBus.$emit("open-modal", {
          component: Solution,
          props: { exam: res.data.solution },
        });
      });
    },
    search(event) {
      if (event.target.value == "") return (this.filteredStudents = this.students);
      this.filteredStudents = this.students.filter((user) => {
        return (
          user.name.toLowerCase().includes(event.target.value) ||
          user.phone.includes(event.target.value)
        );
      });
    },
    async deleteAnswer(item) {
      const id = item._id;
      const confirmed = await confirm("هل تؤكد على المسح نهائيا ؟");
      if (!confirmed) return;
      Axios.delete("/removeUnitExamCorrection/" + id).then((res) => {
        if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);
        this.filter = { ...this.filter };
      });
    },
  },
};
</script>

