<template>
  <div class="examToCorrect bg-light">
    <hr />
    <div class="header">
      <h3 style="color: #555; font-weight: bold">
        {{ examToCorrect.userId | student("fullname") }}
      </h3>
      <h3>({{ examToCorrect.name }}) أمتحان</h3>
      <!-- <h3>({{exam}}) نموذج</h3> -->
      <h4>{{ examToCorrect.stage | stage }}</h4>
      <h4>
        <strong></strong>({{ examToCorrect.year }}/{{ examToCorrect.year + 1 }}) العام
        الدراسي
      </h4>
      <h2 style="color: red">({{ mark }} / {{ fullMark }})</h2>
    </div>
    <hr />
    <div class="_solution">
      <div v-for="(section, s) in examToCorrect.sections" :key="s" class="_section">
        <h3 class="subtitle2">Section ({{ s + 1 }})</h3>
        <display-question
          :type="section.titleType"
          :src="section.text"
          :lang="examToCorrect.lang"
        />
        <div
          v-for="(question, q) in section.questions"
          :key="q"
          class="_question"
          :class="{ ltr: examToCorrect.lang == '2', rtl: examToCorrect.lang != '2' }"
        >
          <div class="question-text">
            <display-question
              :type="question.contentType"
              :src="question.question"
              :lang="examToCorrect.lang"
            />
          </div>
          <!------------------- USER ANSWER ------------------------------->
          <!------------------- USER ANSWER ------------------------------->
          <div class="">
            <label>User answer</label>
            <hr />
            <v-textarea
              v-if="section.questionsType == 0"
              v-model="question.userAnswer"
              label="The Answer"
              auto-grow
              outlined
              readonly
              rows="3"
              row-height="25"
              shaped
            ></v-textarea>

            <v-radio-group
              row
              v-if="section.questionsType == 1"
              v-model="question.userAnswer"
              readonly
            >
              <v-radio
                v-for="(choice, c) in question.choices"
                :key="c"
                :label="choice"
                :value="choice"
              ></v-radio>
            </v-radio-group>
          </div>
          <!------------------- Correct ANSWER ------------------------------->
          <!------------------- Correct ANSWER ------------------------------->
          <div class="">
            <label>Correct answer</label>
            <hr />
            <v-textarea
              v-if="section.questionsType == 0"
              v-model="question.modelAnswer"
              label="The Answer"
              auto-grow
              outlined
              rows="3"
              row-height="25"
              shaped
            ></v-textarea>

            <v-radio-group
              row
              v-if="section.questionsType == 1"
              v-model="question.modelAnswer"
            >
              <v-radio
                v-for="(choice, c) in question.choices"
                :key="c"
                :label="choice"
                :value="choice"
                color="success"
              />
            </v-radio-group>
          </div>
          <div>
            <label>الدرجة</label>
            <hr />
            <div class="row">
              <div class="col-md-3">
                <div class="degree">
                  <integer-plusminus
                    @change="calculateMark()"
                    v-model="question.degree"
                    :min="0"
                    :max="question.fullDegree"
                    :value="0"
                    :increment-aria-label="'increase by 1'"
                    :decrement-aria-label="'decrease by 1'"
                    :spin-button-aria-label="'example two'"
                  ></integer-plusminus>
                  <p style="font-size: 1.5rem; color: #5559" class="m-2">
                    ({{ question.degree }}/{{ question.fullDegree }})
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <hr />

    <div v-if="loading" class="center">
      <loading />
    </div>
    <hr />
    <div class="row justify-content-center p-md-4">
      <div class="col-md-4">
        <button @click="submitCorrection" type="button" class="btn btn-3">
          نشر التصحيح
        </button>
      </div>
      <div class="col-md-4">
        <button
          @click="() => removeCorrection(examToCorrect._id)"
          type="button"
          class="btn btn-5"
        >
          مسح الامتحان للطالب
        </button>
      </div>
    </div>
    <hr />
  </div>
</template>

<script>
import IntegerPlusminus from "../../../components/admin/IntegerPlusMinus";
import axios from "axios";
import { eventBus } from "../../../main";
export default {
  components: {
    IntegerPlusminus,
  },
  created() {
    console.log(this.exam);
  },
  props: ["exam"],
  data() {
    return {
      loading: false,
    };
  },
  computed: {
    examToCorrect() {
      return this.exam;
    },
    fullMark() {
      let mark = 0;
      this.examToCorrect?.sections?.forEach((sec) => {
        sec.questions.forEach((ques) => {
          mark = mark + ques.fullDegree;
        });
      });
      return mark;
    },
    mark() {
      let mark = 0;
      this.examToCorrect?.sections?.forEach((sec) => {
        sec.questions.forEach((ques) => {
          mark = mark + +ques.degree;
        });
      });
      return mark;
    },
    adminData() {
      return this.$store.getters.adminData;
    },
  },
  methods: {
    calculateMark() {
      let mark = 0;
      this.examToCorrect.sections.forEach((sec) => {
        sec.questions.forEach((ques) => {
          mark = mark + +ques.degree;
        });
      });
      this.mark = mark;
    },
    submitCorrection() {
      const correction = {
        ...this.examToCorrect,
        totalDegree: this.mark,
        fullDegree: this.fullMark,
      };
      this.loading = true;
      axios
        .post("/sendUnitExamCorrectionByAdmin/" + this.examToCorrect._id, correction)
        .then((res) => {
          this.loading = false;
          if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);
          this.$store.dispatch("writemessage", "تم حفظ التصحيح...");
        });
    },
    async removeCorrection(id) {
      const confirmed = await confirm("هل تؤكد على المسح نهائيا ؟");
      if (!confirmed) return;
      axios.delete("/removeUnitExamCorrection/" + id).then((res) => {
        if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);
        eventBus.$emit("close-modal");
        eventBus.$emit("delete-solution", id);
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.examToCorrect {
  .header {
    text-align: center;
    @media (max-width: 600px) {
      h3,
      h4,
      h2 {
        font-size: 1.1rem;
      }
    }
  }
}
._solution {
  padding: 16px;
  ._section {
    margin: 30px auto;
  }
}
</style>
